<template>
	<el-container>
		<el-container>
			<el-aside>
				<Aside @get-data="receive" />
			</el-aside>
			<el-main>
				<Main v-if='router==0' />
				<Userdata v-if='router==1' />
				<Userpiece v-if='router==2' />
				<Handbook v-if='router==3' />
				<Recruit v-if='router==4' />
				<Setting v-if='router==5' />
				<Info v-if='router==6' />
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
	import Aside from './Aside.vue'
	import Main from './Main.vue'
	import Info from './Info.vue'
	import Setting from './Setting.vue'
	import Userdata from '../table/userdata.vue'
	import Userpiece from '../table/userpiece.vue'
	import Handbook from '../table/handbook.vue'
	import Recruit from '../table/recruit.vue'
	export default {
		data() {
			return {
				router: 0
			};
		},
		components: {
			Aside,
			Main,
			Setting,
			Info,
			Userdata,
			Userpiece,
			Handbook,
			Recruit
		},
		methods: {
			receive: function(value) {
				this.router = value;
			}
		}
	}
</script>

